import Vue from 'vue'
import Router from 'vue-router'
import Comment from '@/components/Comment'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  routes: [
    {
      path: '/comment',
      name: 'Comment',
      component: Comment
    }
  ]
})



