<template>
  <div class='hello'>
    <VcAFrame title='comments'>
      <VcAColumn size='90%'>
        <VcABox title='comments' :first='true'>
          <el-card>
            <el-form :model='comment'>
              <el-row>
                <el-col :span='1' :offset='0'>
                  <img src='https://img.icons8.com/dotty/80/000000/login-as-user.png'>
                </el-col>
                <el-col :span='21' :offset='1'>
                  <el-row>
                    <span class='user-name'>username</span>
                  </el-row>
                  <el-row>
                    <el-input
                      type='textarea'
                      placeholder='input comment'
                      v-model='comment.text'
                      :maxLength='240'
                    ></el-input>
                  </el-row>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span='3' :offset='20'>
                  <el-button
                    v-if='comment.text'
                    class='vca-button-primary'
                    @click.prevent='submit'
                  >comment</el-button>
                  <el-button
                    v-else
                    class='vca-button-primary'
                    @click.prevent='submit'
                    disabled
                  >comment</el-button>
                </el-col>
              </el-row>
            </el-form>
          </el-card>
          <el-row>
            <el-card v-for='comment in comments' :key='comment.id'>
              <el-row>
                <el-col :span='1'>
                  <img src='https://img.icons8.com/dotty/80/000000/login-as-user.png'>
                </el-col>
                <el-col :span='20' :offset='1'>
                  <el-row>
                    <span class='userName'>{{comment._creator}}</span>
                    <time-ago
                      class='title'
                      :datetime='comment.createdAt'
                      refresh
                      :locale='locale'
                      :long='longString'
                    ></time-ago>
                  </el-row>
                  <el-row>
                    <span>{{comment.text}}</span>
                  </el-row>
                </el-col>
              </el-row>
            </el-card>
          </el-row>
        </VcABox>
      </VcAColumn>
    </VcAFrame>
  </div>
</template>

<script>
import { VcAFrame, VcAColumn, VcABox, VcAInfoBox } from 'vca-widget-base'
import 'vca-widget-base/dist/vca-widget-base.css'
import axios from 'axios'
import TimeAgo from 'vue2-timeago'

export default {
  name: 'Comment',
  components: {
    VcAFrame,
    VcAColumn,
    VcABox,
    VcAInfoBox,
    TimeAgo
  },
  data() {
    return {
      comment: {
        text: ''
      },
      comments: null,
      value: new Date(),
      locale: 'en',
      longString: false,
      tooltip: true
    }
  },
  mounted() {
    //this.$router.push('http://localhost/drops/oauth2/code/get?client_id=comment-backend&response_type=code&state=http://localhost/api/comment/5cd29cda64d95068c5f5002d&redirect_uri=http://localhost/api/oauth/code')
    axios
      .get('/api/comment/5cd29cda64d95068c5f5002d')
      .then(resp => {
        this.comments = resp.data.data
      })
      .catch(err => {
        return err.message
      })
    //Todo: write axios to get current user.
    axios
      .get('/api/comment?poolEvent=5cd29cda64d95068c5f5002d')
      .then(resp => {
        console.log(resp)
      })
      .catch(err => {
        return err.message
      })
  },
  methods: {
    submit() {
      axios
        .post('/api/comment', this.comment)
        .then(resp => {
          console.log(resp)
        })
        .catch(err => {
          console.log(err.message)
        })
    }
  }
}
</script>

<style>
.userName {
  margin-right: 4px;
  margin-bottom: 10px;
  font-size: 1.3rem;
  font-weight: bold;
  line-height: 1.8rem;
}
</style>



